import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import Badge from '@material-ui/core/Badge';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import { fade } from '@material-ui/core/styles/colorManipulator';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MailIcon from '@material-ui/icons/Mail';
import NotificationsIcon from '@material-ui/icons/Notifications';
import MoreIcon from '@material-ui/icons/MoreVert';
import Button from '@material-ui/core/Button';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import Icon from '@material-ui/core/Icon';
import classNames from 'classnames';
import { isMobile } from "react-device-detect";
import Dashboard from './../dashboard/dashboard';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import withStyles from '@material-ui/core/styles/withStyles';
import SketchPadcomp from './../draw/SketchPadcomp';
import {
  Container, Row, Col, Navbar, NavbarBrand, Nav, NavItem, NavLink, Card, CardBody, CardTitle, CardSubtitle, CardText,
} from 'reactstrap';

import Login from './../login/login';
import Register from './../register/register';
import { loadCSS } from 'fg-loadcss/src/loadCSS';

const useStyles = theme => ({
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(3),
      width: 'auto',
    },
  },
  searchIcon: {
    width: theme.spacing(7),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 7),
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: 200,
    },
  },
  sectionDesktop: {
    display: 'none',
    [theme.breakpoints.up('md')]: {
      display: 'flex',
    },
  },
  sectionMobile: {
    display: 'flex',
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
  root: {
    width: '100%',
    flexGrow: 1,
    backgroundColor: '#C0C0C0',
  },
  table: {
    minWidth: 1020,
  },
  tableWrapper: {
    overflowX: 'auto',
  },
  grow: {
    flexGrow: 1,
  },
  layout: {
    width: 'auto',
    display: 'block', // Fix IE 11 issue.
    marginLeft: theme.spacing(3),
    marginRight: theme.spacing(3),
    [theme.breakpoints.up(400 + theme.spacing(6))]: {
      width: 400,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: `${theme.spacing(2)}px ${theme.spacing(3)}px ${theme.spacing(3)}px`,
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    marginTop: theme.spacing(3),
  },
  centeralign: {
    flexGrow: 1,
    textAlign: 'center',
  },
  contentbody: {
    height: '85vh'
  },
});

class PrimarySearchAppBar extends React.Component {
  
  i = 0;
  constructor(props) {
        super(props);

        this.email = React.createRef();
        this.password = React.createRef();
        this.name = React.createRef();
  }

  componentDidMount() {
    loadCSS(
      'https://use.fontawesome.com/releases/v5.1.0/css/all.css',
      document.querySelector('#insertion-point-jss'),
    );
    //localStorage.setItem('authlogin', 'false');
    if(!isMobile && localStorage.getItem('authlogin') !== 'true'){
      localStorage.setItem('authlogin', 'false');
    }
  }

  state = {
    anchorEl: null,
    mobileMoreAnchorEl: null,
    showdrawpad: true,
  };

  handleProfileMenuOpen = (event) => {
    this.setState({ anchorEl: event.currentTarget });
  }

  handleMenuClose = () => {
    this.setState({ anchorEl: null });
    this.state.handleMobileMenuClose();
  }

  handleMobileMenuOpen = (event) => {
    this.setState({ mobileMoreAnchorEl: event.currentTarget });
  }

  handleMobileMenuClose = () => {
    this.setState({ mobileMoreAnchorEl: null });
  }

  handleClose = () => {
    this.setState({ anchorEl: null });
    this.handleMobileMenuClose();
  }

  userauth_logout = () => {
    localStorage.setItem('authlogin', 'false');
    localStorage.setItem('user_id', '');
    alert('logout!');
    //window.location.reload();
    window.location.href = "/reactdraw/";
  }

  hiddensidebar = () => {
    if(this.i === 0){
      localStorage.setItem('hiddensidebar',false);
      this.i = 1;
      //alert(localStorage.getItem('hiddensidebar'));
      // Force a render without state change...
      this.forceUpdate(); //refresh component  or
      // Force a render with a simulated state change
      //this.setState({ state: this.state }); //refresh component 
    } else if(this.i === 1){
      localStorage.setItem('hiddensidebar',true);
      this.i = 0;
      //alert(localStorage.getItem('hiddensidebar'));
      // Force a render without state change...
      this.forceUpdate(); //refresh component  or
      // Force a render with a simulated state change
      //this.setState({ state: this.state }); //refresh component
    }
  }

  setshowdrawpad = () => {
    //alert('setshowdrawpad');
    this.setState( { showdrawpad  : true } );
  }

  sethidedrawpad = () => {
    //alert('sethidedrawpad');
    this.setState( { showdrawpad  : false } );
  }

  render() {
    const { anchorEl, mobileMoreAnchorEl } = this.state;
    const { classes } = this.props;
    const isMenuOpen = Boolean(anchorEl);
    const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);
      
  const renderMenu = (
    <Grid container className={classes.root} spacing={12}>
      <Grid item xs={12}>
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={this.isMenuOpen}
      onClose={this.handleMenuClose}
    >
      <MenuItem onClick={this.handleClose}>Profile</MenuItem>
      <MenuItem onClick={this.handleClose}>My account</MenuItem>
    </Menu>
    </Grid>
    </Grid>
  );

  const renderMobileMenu = (
    <Grid container className={classes.root} spacing={12}>
      <Grid item xs={12}>
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMobileMenuOpen}
      onClose={this.handleMobileMenuClose}
    >
      <MenuItem>
      <Button color="inherit" component={Link} to="/reactdraw/" onClick={this.handleMobileMenuClose}>
          <Icon className={classNames(classes.icon, 'fa fa-home')} /><p>Home</p>
      </Button>
      </MenuItem>
      <MenuItem>
      <IconButton color="inherit" component={Link} to="/reactdraw/about" onClick={this.handleMobileMenuClose}>
          <Icon className={classNames(classes.icon, 'fa fa-info-circle')} /><p>About</p>
      </IconButton>
      </MenuItem>
      {localStorage.getItem('authlogin')==='false' && (
      <MenuItem>
      <IconButton color="inherit" component={Link} to="/reactdraw/login" onClick={this.handleMobileMenuClose}>
          <Icon className={classNames(classes.icon, 'fa fa-sign-in-alt')} /><p>Login</p>
      </IconButton>
      </MenuItem>
      )}
      {localStorage.getItem('authlogin')==='false' && (
      <MenuItem>
      <IconButton color="inherit" component={Link} to="/reactdraw/register" onClick={this.handleMobileMenuClose}>
          <Icon className={classNames(classes.icon, 'fa fa-user-plus')} /><p>Register</p>
      </IconButton>
      </MenuItem>
      )}
      {localStorage.getItem('authlogin')==='true' && (
      <MenuItem>
      <IconButton color="inherit" component={Link} to="/reactdraw/dashboard" onClick={this.handleMobileMenuClose}>
          <Icon className={classNames(classes.icon, 'fa fa-tachometer-alt')} /><p>Dashboard</p>
      </IconButton>
      </MenuItem>
      )}
      {localStorage.getItem('authlogin')==='true' && (
      <MenuItem>
      <IconButton color="inherit" onClick={this.userauth_logout}>
          <Icon className={classNames(classes.icon, 'fa fa-sign-out-alt')} /><p>Logout</p>
      </IconButton>
      </MenuItem>
      )}
      {localStorage.getItem('authlogin')==='true' && (
      <MenuItem>
        <IconButton color="inherit">
          <Badge className={classes.margin} badgeContent={4} color="secondary">
            <MailIcon />
          </Badge>
        </IconButton>
        <p>Messages</p>
      </MenuItem>
      )}
      {localStorage.getItem('authlogin')==='true' && (
      <MenuItem>
        <IconButton color="inherit">
          <Badge className={classes.margin} badgeContent={11} color="secondary">
            <NotificationsIcon />
          </Badge>
        </IconButton>
        <p>Notifications</p>
      </MenuItem>
      )}
      {localStorage.getItem('authlogin')==='true' && (
      <MenuItem onClick={this.handleProfileMenuOpen}>
        <IconButton color="inherit">
          <AccountCircle />
        </IconButton>
        <p>Profile</p>
      </MenuItem>
      )}
    </Menu>
    </Grid>
    </Grid>
  );

  const renderPaper = (
    <Paper className={classes.root}>
      <Container>
          <SketchPadcomp/>
        </Container>
    </Paper>
  );

  const Home = () => (
    <div>
      <h2>Home</h2>
      <hr/>
    </div>
  );

  const About = () => (
    <div>
      <h2>About</h2>
      <hr/>
      <Grid container spacing={2} justify="center">
            <Grid item xs={6}>
              <div className={classes.centeralign}><h1><u>About Page</u></h1></div>
              <p><strong><u>Contact at:</u> </strong><a href="mailto:claudiu.vasile@gmail.com?Subject=Hello%20React%20Material">Mail to: claudiu.vasile@gmail.com</a></p>
        </Grid>
      </Grid>
    </div>
  );

  return (
    <div className={classes.root}>
      <Router>
          <div className={isMobile?'':classes.contentbody}>
          <AppBar position="static">
          <Toolbar>
            <IconButton className={classes.menuButton} color="inherit" aria-label="Open drawer" onClick={(event) => { this.hiddensidebar(); this.setshowdrawpad(); }}>
              <MenuIcon />
            </IconButton>
            <Typography className={classes.title} variant="h6" color="inherit" noWrap>
              Material-UI
            </Typography>
            <div className={classes.search}>
              <div className={classes.searchIcon}>
                <SearchIcon />
              </div>
              <InputBase
                placeholder="Search"
                classes={{
                  root: classes.inputRoot,
                  input: classes.inputInput,
                }}
              />
            </div>
            <div className={classes.grow} />
            <div className={classes.sectionDesktop}>
              <Button color="inherit" component={Link} to="/reactdraw/" onClick={this.setshowdrawpad} >
                  <Icon className={classNames(classes.icon, 'fa fa-home')} />
              </Button>
              <IconButton color="inherit" component={Link} to="/reactdraw/about" onClick={this.sethidedrawpad} >
                  <Icon className={classNames(classes.icon, 'fa fa-info-circle')} />
              </IconButton>
              {localStorage.getItem('authlogin')==='true' && (
              <IconButton color="inherit" component={Link} to="/reactdraw/dashboard" onClick={this.sethidedrawpad} >
                  <Icon className={classNames(classes.icon, 'fa fa-tachometer-alt')} />
              </IconButton>
              )}
              {localStorage.getItem('authlogin')==='true' && (
              <IconButton color="inherit" onClick={this.userauth_logout} >
                  <Icon className={classNames(classes.icon, 'fa fa-sign-out-alt')} />
              </IconButton>
              )}
              {localStorage.getItem('authlogin')==='true' && (
              <IconButton color="inherit">
                <Badge className={classes.margin} badgeContent={4} color="secondary">
                  <MailIcon />
                </Badge>
              </IconButton>
              )}
              {localStorage.getItem('authlogin')==='true' && (
              <IconButton color="inherit">
                <Badge className={classes.margin} badgeContent={17} color="secondary">
                  <NotificationsIcon />
                </Badge>
              </IconButton>
              )}
              {localStorage.getItem('authlogin')==='true' && (
              <IconButton
                aria-owns={isMenuOpen ? 'material-appbar' : null}
                aria-haspopup="true"
                onClick={this.handleProfileMenuOpen}
                color="inherit"
              >
              <AccountCircle />
              </IconButton>
              )}
              {localStorage.getItem('authlogin')==='false' && (
              <IconButton color="inherit" component={Link} to="/reactdraw/login" onClick={this.sethidedrawpad} >
                  <Icon className={classNames(classes.icon, 'fa fa-sign-in-alt')} />
              </IconButton>
              )}
              {localStorage.getItem('authlogin')==='false' && (
              <IconButton color="inherit" component={Link} to="/reactdraw/register" onClick={this.sethidedrawpad} >
                  <Icon className={classNames(classes.icon, 'fa fa-user-plus')} />
              </IconButton>
              )}
            </div>
            <div className={classes.sectionMobile}>
              <IconButton aria-haspopup="true" onClick={this.handleMobileMenuOpen} color="inherit">
                <MoreIcon />
              </IconButton>
            </div>
          </Toolbar>
        </AppBar>
        {renderMenu}
        {renderMobileMenu}

            <Route exact path="/reactdraw/" component={Home} />
            {this.state.showdrawpad?renderPaper:''}
            <Route path="/reactdraw/about" component={About} />
            <Route path="/reactdraw/login" component={Login} />
            <Route path="/reactdraw/register" component={Register} />
            <Route path="/reactdraw/dashboard" component={Dashboard} />
          </div>
        </Router>
    </div>
  );
    }
}

export default withStyles(useStyles)(PrimarySearchAppBar);