import React, { Component } from 'react';
import { SketchPad, TOOL_PENCIL, TOOL_LINE, TOOL_RECTANGLE, TOOL_ELLIPSE, TOOL_TEXT } from './index';
import { FILL_TEXT } from './tools/Text';
import { PDFDocument, StandardFonts, rgb } from 'pdf-lib';
import { download } from 'downloadjs';
//import IO from 'socket.io-client';
import {
  Container, Row, Col, Navbar, NavbarBrand, Nav, NavItem, NavLink, Card, CardBody, CardTitle, CardSubtitle, CardText, Button
} from 'reactstrap';

//const wsClient = IO(`ws://127.0.0.1:12346`);

export default class SketchPadcomp extends Component
{
  //socket = null;

  constructor(props) {
    super(props);
    this.myInput = React.createRef();

    this.state = {
      tool:TOOL_PENCIL,
      size: 2,
      color: '#000000',
      fill: false,
      fillColor: '#444444',
      filltext: FILL_TEXT,
      items: [],
      docUrl:''
    }
  }

  componentDidMount() {
    //wsClient.on('addItem', item => this.setState({items: this.state.items.concat([item])}));
  }

  fixType (type) {
    //alert(JSON.stringify(type));
    type = type.toString().toLowerCase().replace(/jpg/i, 'jpeg');
    //alert(type);
		var r = type.match(/png|jpeg|bmp|gif/)[0];
		return 'image/' + r;
  }

  getImageData (canvas) {
		var w = canvas.width,
			h = canvas.height;
		return canvas.getContext('2d').getImageData(0, 0, w, h);
  }
  
  genBitmapImage(oData) {

		var biWidth  = oData.width;
		var biHeight	= oData.height;
		var biSizeImage = biWidth * biHeight * 3;
		var bfSize  = biSizeImage + 54; // total header size = 54 bytes

		var BITMAPFILEHEADER = [
			// WORD bfType -- The file type signature; must be "BM"
			0x42, 0x4D,
			// DWORD bfSize -- The size, in bytes, of the bitmap file
			// eslint-disable-next-line no-mixed-operators
			bfSize & 0xff, bfSize >> 8 & 0xff, bfSize >> 16 & 0xff, bfSize >> 24 & 0xff,
			// WORD bfReserved1 -- Reserved; must be zero
			0, 0,
			// WORD bfReserved2 -- Reserved; must be zero
			0, 0,
			// DWORD bfOffBits -- The offset, in bytes, from the beginning of the BITMAPFILEHEADER structure to the bitmap bits.
			54, 0, 0, 0
		];

		
		var BITMAPINFOHEADER = [
			// DWORD biSize -- The number of bytes required by the structure
			40, 0, 0, 0,
      // LONG biWidth -- The width of the bitmap, in pixels
      // eslint-disable-next-line no-mixed-operators
			biWidth & 0xff, biWidth >> 8 & 0xff, biWidth >> 16 & 0xff, biWidth >> 24 & 0xff,
      // LONG biHeight -- The height of the bitmap, in pixels
      // eslint-disable-next-line no-mixed-operators
			biHeight & 0xff, biHeight >> 8  & 0xff, biHeight >> 16 & 0xff, biHeight >> 24 & 0xff,
			// WORD biPlanes -- The number of planes for the target device. This value must be set to 1
			1, 0,
			// WORD biBitCount -- The number of bits-per-pixel, 24 bits-per-pixel -- the bitmap
			// has a maximum of 2^24 colors (16777216, Truecolor)
			24, 0,
			// DWORD biCompression -- The type of compression, BI_RGB (code 0) -- uncompressed
			0, 0, 0, 0,
      // DWORD biSizeImage -- The size, in bytes, of the image. This may be set to zero for BI_RGB bitmaps
      // eslint-disable-next-line no-mixed-operators
			biSizeImage & 0xff, biSizeImage >> 8 & 0xff, biSizeImage >> 16 & 0xff, biSizeImage >> 24 & 0xff,
			// LONG biXPelsPerMeter, unused
			0,0,0,0,
			// LONG biYPelsPerMeter, unused
			0,0,0,0,
			// DWORD biClrUsed, the number of color indexes of palette, unused
			0,0,0,0,
			// DWORD biClrImportant, unused
			0,0,0,0
		];

		var iPadding = (4 - ((biWidth * 3) % 4)) % 4;

		var aImgData = oData.data;

		var strPixelData = '';
		var biWidth4 = biWidth<<2;
		var y = biHeight;
		var fromCharCode = String.fromCharCode;

		do {
			var iOffsetY = biWidth4*(y-1);
			var strPixelRow = '';
			for (var x = 0; x < biWidth; x++) {
				var iOffsetX = x<<2;
				strPixelRow += fromCharCode(aImgData[iOffsetY+iOffsetX+2]) +
							   fromCharCode(aImgData[iOffsetY+iOffsetX+1]) +
							   fromCharCode(aImgData[iOffsetY+iOffsetX]);
			}

			for (var c = 0; c < iPadding; c++) {
				strPixelRow += String.fromCharCode(0);
			}

			strPixelData += strPixelRow;
		} while (--y);

		var strEncoded = this.encodeData(BITMAPFILEHEADER.concat(BITMAPINFOHEADER)) + this.encodeData(strPixelData);

		return strEncoded;
  }
  
  encodeData (data) {
		// eslint-disable-next-line no-throw-literal
		if (!window.btoa) { throw 'btoa undefined' }
		var str = '';
		if (typeof data == 'string') {
			str = data;
		} else {
			for (var i = 0; i < data.length; i ++) {
				str += String.fromCharCode(data[i]);
			}
		}

		return btoa(str);
  }
  
  saveFile (strData) {
    //alert(strData);
    //  document.location.href = strData;
    document.write('<img src="'+strData+'"/>');
  }

  scaleCanvas (canvas, width, height) {
		var w = canvas.width,
			h = canvas.height;
		if (width === undefined) {
			width = w;
		}
		if (height === undefined) {
			height = h;
		}

		var retCanvas = document.createElement('canvas');
		var retCtx = retCanvas.getContext('2d');
		retCanvas.width = width;
		retCanvas.height = height;
		retCtx.drawImage(canvas, 0, 0, w, h, 0, 0, width, height);
		return retCanvas;
  }
  
  makeURI (strData, type) {
		return 'data:' + type + ';base64,' + strData;
  }
  
  getDataURL (canvas, type, width, height) {
		canvas = this.scaleCanvas(canvas, width, height);
		return canvas.toDataURL(type);
	}
  
  saveAsImage(canvas, width, height, type) {
    var downloadMime = 'image/octet-stream';
    //var downloadMime = ['image/png', 'image/bmp', 'image/gif', 'image/jpeg', 'image/tiff'];
    //alert(downloadMime[1]);
    //var canvasget = document.getElementById('canvas');
    //alert(canvasget.toDataURL());
    //type = document.getElementById('sel');
    //alert(type.value);
		if (canvas && canvas.toDataURL()) {
      //alert(JSON.stringify(type));
			if (typeof canvas == "string") { canvas = document.getElementById(canvas); }
      if (type === undefined) { type = 'png'; }
      //alert(JSON.stringify(type));
			type = this.fixType(type);
			if (/bmp/.test(type)) {
				var data = this.getImageData(this.scaleCanvas(canvas, width, height));
				var strData = this.genBitmapImage(data);
				this.saveFile(this.makeURI(strData, downloadMime));
			} else {
        var strDatad=this.getDataURL(canvas, type, width, height);
        //alert(strDatad);
				this.saveFile(strDatad.replace(type, downloadMime));
			}
		}
	};

  savecanvas() {
  var canvas = document.getElementById('canvas');
  var sel = document.getElementById('sel').value;
  //alert(sel.value);
  //alert(this.myInput.current.offsetWidth);
  //alert(this.myInput.current.offsetHeight);
  //Canvas2Image.saveAsImage(canvas, canvas.width, canvas.height, sel);
  this.saveAsImage(canvas, this.myInput.current.offsetWidth, this.myInput.current.offsetHeight, sel);
  }

  clearcanvas() {
    var canvas = document.getElementById('canvas');
    const context = canvas.getContext('2d');

    context.clearRect(0, 0, canvas.width, canvas.height);
  }

  async pdfaddcanvas(){
    var canvas = document.getElementById('canvas');
    const context = canvas.getContext('2d');
    var sel = document.getElementById('sel').value;

    // Create a new PDFDocument
    const pdfDoc = await PDFDocument.create()

    // Embed the Times Roman font
    const timesRomanFont = await pdfDoc.embedFont(StandardFonts.TimesRoman)

    // Add a blank page to the document
    const page = pdfDoc.addPage()

    // Get the width and height of the page
    const { width, height } = page.getSize()

    //const jpgUrl = this.getDataURL(canvas, sel, this.myInput.current.offsetWidth, this.myInput.current.offsetHeight);
    //const jpgUrl = 'https://pdf-lib.js.org/assets/cat_riding_unicorn.jpg';
    var downloadMime = 'image/octet-stream';
    var strDatad=this.getDataURL(canvas, sel, this.myInput.current.offsetWidth, this.myInput.current.offsetHeight);
    const data = strDatad.replace(sel, downloadMime);
    //var bytes = new Uint8Array(jpgByte);
    var bytes = new Uint8Array(data.length / 2);

    for (var i = 0; i < data.length; i += 2) {
        bytes[i / 2] = parseInt(data.substring(i, i + 2), 16);
    }
    const jpgUrl = URL.createObjectURL(
      new Blob([bytes], { type: "image/bmp" })
    );
    //alert(JSON.stringify(jpgUrl));

    //const jpgImageBytes = await fetch(jpgUrl).then((res) => res.arrayBuffer());
    //const jpgImage = await pdfDoc.embedJpg(jpgImageBytes);
    // Draw a string of text toward the top of the page
    const fontSize = 30;
    page.drawText(this.state.filltext, {
      x: 50,
      y: height - 4 * fontSize,
      size: fontSize,
      font: timesRomanFont,
      color: rgb(0, 0.53, 0.71),
    });

    // Serialize the PDFDocument to bytes (a Uint8Array)
    const pdfBytes = await pdfDoc.save();

    // Trigger the browser to download the PDF document
    //download(pdfBytes, "pdf-lib_creation_example.pdf", "application/pdf");
    var bytes = new Uint8Array(pdfBytes); 
    const docUrl = URL.createObjectURL(
      new Blob([bytes], { type: "application/pdf" })
    );
    
    this.setState({docUrl: docUrl});
    //alert(JSON.stringify(docUrl));
  }
  //seltool(toolsel) {
    //alert('Select tool'+tool);
  //  this.setState({tool: toolsel});
  //}

  render() {
    const { tool, size, color, fill, fillColor, filltext, items, docUrl } = this.state;
    return (
      <div style={{marginTop:-50, marginBottom:0}}>
        <h2>React Draw Pad</h2>
        <Row className="xsbt">
        <Col xs="12" md="6" lg="4">
        <div ref={this.myInput} className="divcansas" style={{float:'left', marginTop:-20}}>
          <SketchPad
            width={500}
            height={500}
            animate={true}
            size={size}
            color={color}
            fillColor={fill ? fillColor : ''}
            fillText={filltext}
            items={items}
            tool={tool}
            //onCompleteItem={(i) => wsClient.emit('addItem', i)}
          />
        </div>
        </Col>
        <Col xs="12" md="6" lg="4 offset-1">
        <div style={{float:'left', position:'relative'}}>
          <div className="tools" style={{marginBottom:20}}>
          {localStorage.getItem('authlogin')==='true' && (
            <Col xs="3">
              <button className="btn btn-primary"
                onClick={() => this.savecanvas()}
              >Save</button>
              <select id="sel" className="form-control">
                  <option value="png">png</option>
                  <option value="jpeg">jpeg</option>
                  <option value="bmp">bmp</option>
              </select>
            </Col>
            )}
            <Col xs="3">
              <button className="btn btn-primary"
                onClick={(event) => { this.clearcanvas(); }}
                >Clear</button>
            </Col>
            {localStorage.getItem('authlogin')==='true' && (
            <Col xs="3">
              <button className="btn btn-primary"
                onClick={(event) => { this.pdfaddcanvas(); }}
                >Pdf</button>
                <a href={docUrl}>Pdf</a>
            </Col>
            )}
            <Col xs="3">
              <select id="seltool" className="form-control" onChange={(e) => this.setState({tool: e.target.value}) }>
                  <option value={TOOL_PENCIL}>Pencil</option>
                  <option value={TOOL_LINE}>Line</option>
                  <option value={TOOL_ELLIPSE}>Ellipse</option>
                  <option value={TOOL_RECTANGLE}>Rectangle</option>
                  <option value={TOOL_TEXT}>Text</option>
              </select>
            </Col>
          </div>
          <div className="options" style={{marginBottom:20}}>
            <label htmlFor="">size: </label>
            <input min="1" max="20" type="range" value={size} onChange={(e) => this.setState({size: parseInt(e.target.value)})} />
          </div>
          <div className="options" style={{marginBottom:20}}>
            <label htmlFor="">color: </label>
            <input type="color" value={color} onChange={(e) => this.setState({color: e.target.value})} />
          </div>
          {(this.state.tool === TOOL_ELLIPSE || this.state.tool === TOOL_RECTANGLE || this.state.tool === TOOL_TEXT) ?
            <div className="options">
              <label htmlFor="">fill in:&nbsp;&nbsp;</label>
              <input type="checkbox" value={fill} style={{margin:'0 8'}}
                     onChange={(e) => this.setState({fill: e.target.checked})} />
              {fill ? <span>
                  <label htmlFor="">with color:&nbsp;&nbsp;</label>
                  <input type="color" value={fillColor} onChange={(e) => this.setState({fillColor: e.target.value})} />
                </span> : ''}
            </div> : ''}
            {(this.state.tool === TOOL_TEXT) ?
              <div>
              <label htmlFor="">Text:</label>
              <textarea type="text" value={filltext} style={{margin:'0 8'}}
                     onChange={(e) => this.setState({filltext: e.target.value})} />
              </div> : ''}
        </div>
        </Col>
        </Row>
      </div>
    );
  }
}

