import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { withStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import { secondaryListItems, dashboard } from './listItems';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import DashboardIcon from '@material-ui/icons/Dashboard';
import ListItemText from '@material-ui/core/ListItemText';
import PeopleIcon from '@material-ui/icons/People';
import BarChartIcon from '@material-ui/icons/BarChart';
import LayersIcon from '@material-ui/icons/Layers';
import Icon from '@material-ui/core/Icon';

const drawerWidth = 240;

const icon = {
  marginLeft: 10,
};

const styles = theme => ({
  root: {
    display: 'flex',
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginLeft: 12,
    marginRight: 36,
  },
  menuButtonHidden: {
    display: 'none',
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9),
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    height: '77vh',
    overflow: 'auto',
  },
  chartContainer: {
    marginLeft: -22,
  },
  tableContainer: {
    height: 320,
  },
  h5: {
    marginBottom: theme.spacing(2),
  },
});

class Dashboard extends React.Component {
  constructor(props) {
    super(props);

    if (localStorage.getItem('p1_token') === 'undefined' || localStorage.getItem('p2_token') === 'undefined'){
    localStorage.setItem('dashboardclick', 'true');
    localStorage.setItem('chatclick', 'false');
    localStorage.setItem('chessclick', 'false');
    } else if (localStorage.getItem('p1_token') !== 'undefined' || localStorage.getItem('p2_token') !== 'undefined'){
    localStorage.setItem('dashboardclick', 'false');
    localStorage.setItem('chatclick', 'false');
    localStorage.setItem('chessclick', 'true');
    }
    console.log(localStorage.getItem('authlogin'));
  };

  state = {
    open: false,
  };

  handleDrawerOpen = () => {
    this.setState({ open: true });
  };

  handleDrawerClose = () => {
    this.setState({ open: false });
  };

  dashrefresh = () => {
    this.forceUpdate();
  }

  render() {
    const { classes } = this.props;

    return (
      <React.Fragment>
        <CssBaseline />
        {localStorage.getItem('authlogin')==='true' && (<div className={classes.root}>
          {localStorage.getItem('hiddensidebar')==='true' && (<Drawer
            variant="permanent"
            classes={{
              paper: classNames(classes.drawerPaper, !this.state.open && classes.drawerPaperClose),
            }}
            open={this.state.open} >
            {this.state.open && (<div className={classes.toolbarIcon}>
              <IconButton onClick={this.handleDrawerClose}>
                <ChevronLeftIcon />
              </IconButton>
            </div>)}
            {this.state.open === false && (<div className={classes.toolbarIcon}>
              <IconButton onClick={this.handleDrawerOpen}>
                <ChevronRightIcon />
              </IconButton>
            </div>)}
            <Divider />
            <List>
              <div>
                <ListItem button>
                  <ListItemIcon>
                    <DashboardIcon onClick={(e) => {
                      localStorage.setItem('dashboardclick', 'true');
                      localStorage.setItem('chatclick', 'false');
                      localStorage.setItem('chessclick', 'false');
                      alert('Dashboard click!!'+localStorage.getItem('dashboardclick'));
                      this.forceUpdate();
                    }}/>
                  </ListItemIcon>
                  <ListItemText primary="Dashboard" onClick={(e) => {
                      localStorage.setItem('dashboardclick', 'true');
                      localStorage.setItem('chatclick', 'false');
                      localStorage.setItem('chessclick', 'false');
                      alert('Dashboard click!!'+localStorage.getItem('dashboardclick'));
                      this.forceUpdate();
                    }} />
                </ListItem>
                <ListItem button>
                  <ListItemIcon>
                    <Icon className={classNames(icon, 'fa fa-comments')} onClick={(e) => {
                    localStorage.setItem('chatclick', 'true');
                    localStorage.setItem('chessclick', 'false');
                    localStorage.setItem('dashboardclick', 'false');
                    alert('Chat click!!'+localStorage.getItem('chatclick'));
                    this.forceUpdate();
                    }}/>
                  </ListItemIcon>
                  <ListItemText primary="Chat" onClick={(e) => {
                    localStorage.setItem('chatclick', 'true');
                    localStorage.setItem('chessclick', 'false');
                    localStorage.setItem('dashboardclick', 'false');
                    alert('Chat click!!'+localStorage.getItem('chatclick'));
                    this.forceUpdate();
                    }}/>
                </ListItem>
                <ListItem button>
                  <ListItemIcon>
                    <Icon className={classNames(icon, 'fa fa-chess')} onClick={(e) => {
                    localStorage.setItem('chessclick', 'true');
                    localStorage.setItem('chatclick', 'false');
                    localStorage.setItem('dashboardclick', 'false');
                    alert('Chess click!!'+localStorage.getItem('chessclick'));
                    this.forceUpdate();
                    }}/>
                  </ListItemIcon>
                  <ListItemText primary="Chess" onClick={(e) => {
                    localStorage.setItem('chessclick', 'true');
                    localStorage.setItem('chatclick', 'false');
                    localStorage.setItem('dashboardclick', 'false');
                    alert('Chess click!!'+localStorage.getItem('chessclick'));
                    this.forceUpdate();
                    }}/>
                </ListItem>
                <ListItem button>
                  <ListItemIcon>
                    <PeopleIcon />
                  </ListItemIcon>
                  <ListItemText primary="Customers" />
                </ListItem>
                <ListItem button>
                  <ListItemIcon>
                    <BarChartIcon />
                  </ListItemIcon>
                  <ListItemText primary="Reports" />
                </ListItem>
                <ListItem button>
                  <ListItemIcon>
                    <LayersIcon />
                  </ListItemIcon>
                  <ListItemText primary="Integrations" />
                </ListItem>
              </div>
            </List>
            <Divider />
            <List>{secondaryListItems}</List>
          </Drawer>)}
          <main className={classes.content}>
            {localStorage.getItem('dashboardclick')==='true' && dashboard}
          </main>
        </div>)}
      </React.Fragment>
    );
  }
}

Dashboard.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Dashboard);

