import React from 'react';

import classNames from 'classnames';
import CssBaseline from '@material-ui/core/CssBaseline';
import Paper from '@material-ui/core/Paper';
import Avatar from '@material-ui/core/Avatar';
import Icon from '@material-ui/core/Icon';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import withStyles from '@material-ui/core/styles/withStyles';

import firebase from './../firebase.js';
import $ from 'jquery';

const styles = theme => ({
    root: {
      width: '100%',
    },
    layout: {
        width: 'auto',
        display: 'block', // Fix IE 11 issue.
        marginLeft: theme.spacing(3),
        marginRight: theme.spacing(3),
        [theme.breakpoints.up(400 + theme.spacing(6))]: {
          width: 400,
          marginLeft: 'auto',
          marginRight: 'auto',
        },
      },
      paper: {
        marginTop: theme.spacing(3),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: `${theme.spacing(2)}px ${theme.spacing(3)}px ${theme.spacing(3)}px`,
      },
      avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
      },
      form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
      },
      submit: {
        marginTop: theme.spacing(3),
      },
      centeralign: {
        flexGrow: 1,
        textAlign: 'center',
      },
      contentbody: {
        height: '85vh',
      }
});

class Register extends React.Component {
    constructor(props) {
        super(props);

        this.email = React.createRef();
        this.password = React.createRef();
    }

    state = {
        errorlogin: '',
    }

    componentDidMount() {
      $("div.container").css({display:'none'});
    }

    register = (e) => {
        e.preventDefault();
        //alert('S-a facut Register!!'+this.name.current.value+'-'+this.email.current.value+'-'+this.password.current.value);
        let users = firebase.database().ref("/users");
        let countusers = 0;
        users.orderByChild("id").on('child_added', function (snap) {
          let user = snap.val(); 
          if(typeof(user.id) !== 'undefined'){
          //console.log(user.id);
          countusers = countusers + 1;
          }
          });
          setTimeout(function(){ 
          //alert(countusers);
          countusers = countusers + 1;
          var today = new Date();
          users.push({id: countusers,name: this.name.current.value,email: this.email.current.value,password: this.password.current.value,created_at: today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate(),updated_at: today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate() });
          window.location.href = "/reactdraw/login";
          }.bind(this), 3000);
        }
      

    render() {
        const { classes } = this.props;

        return (
            <div>
            <h2>Register</h2>
            <hr/>
            <React.Fragment>
            <CssBaseline />
            <main className={classes.layout}>
            <Paper className={classes.paper}>
                <Avatar className={classes.avatar}>
                <Icon className={classNames(classes.icon, 'fa fa-user-plus')} />
                </Avatar>
                <Typography component="h1" variant="h5">
                Register
                </Typography>
                <div className={classes.errorlogin}>{this.state.errorlogin}</div>
                <form className={classes.form} method="post" onSubmit={this.register.bind(this)}>
                <FormControl margin="normal" required fullWidth>
                    <InputLabel htmlFor="name">User Name</InputLabel>
                    <Input
                    type="name"
                    id="name" 
                    name="name"
                    inputRef={this.name}
                    />
                </FormControl>
                <FormControl margin="normal" required fullWidth>
                    <InputLabel htmlFor="email">Email Address</InputLabel>
                    <Input
                    type="email"
                    id="email" 
                    name="email"
                    inputRef={this.email}
                    />
                </FormControl>
                <FormControl margin="normal" required fullWidth>
                    <InputLabel htmlFor="password">Password</InputLabel>
                    <Input
                    name="password"
                    type="password"
                    id="password"
                    inputRef={this.password}
                    />
                </FormControl>
                <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                >
                    Register
                </Button>
                </form>
            </Paper>
            </main>
        </React.Fragment>
            </div>
         );
        }
    }
    
    export default withStyles(styles)(Register);
        